#map {
  fill: #e8e7e7;
  stroke: gray;
  stroke: black;

  width: 100%;
  height: 600px;
  stroke-width: 0.3;

  /*   stroke-dasharray: 1; */
}

#map svg {
  width: 100%;
  height: calc(100% - 45px);
}

#tooltip {
  background: #f5ebe0;
  border: solid 2px gray;
  padding: 3px;
}
.path {
  fill: rgb(238, 253, 230);
  stroke: rgb(4, 3, 1);
  stroke-width: 1px;
}

.path:hover {
  cursor: pointer;
  fill-opacity: 0.8;
}

.case {
  fill-opacity: 0.7;
  stroke-width: 2px;
}

.case:hover {
  cursor: pointer;
  fill: #d07000;
  fill-opacity: 1;
}

.slider {
  margin-left: 5%;
  width: 80%;
}

#legend {
  width: 80%;
}
.circle-legend {
  display: flex;
  flex-direction: column;
  width: 15px;
  height: 15px;

  opacity: 70%;
  border-radius: 50%;
}
